<template>
  
  
  <div class="content" >
      <van-config-provider  :theme-vars="themeVars" theme="dark">
        <van-grid :gutter="20" :clickable="true" :column-num="5">
          <van-grid-item   
          v-for="(d,i) in friendLinks"
          :key="i"
          :text="d.text" 
          @click="goFriend(d.link)"
          class="hover-hand" />

        </van-grid>
      </van-config-provider>
  </div>

</template>

<script>
  import {mapState} from 'vuex';
  // import friendLinks from '@/assets/friendLinks.json'
  import * as api from '@/api'
  export default {
    name: "index",
    components: {},
    data() {
      return {
        type: 'policy',
        themeVars:{
          gridItemContentBackground: "#191d25",
          gridItemContentActiveColor: "red",
          gridItemTextFontSize: "20px"
        },
        friendLinks:[]
      }
    },
    computed: {
      ...mapState({
        language: state => state.common.language,
      }),
    },
    watch: {
      '$route': {
        handler() {
          this.type = this.$route.query.type
        },
        immediate: true
      },
    },
    methods: {
      goFriend(url) {
        console.log(url);
        window.open(url,"_blank");
      },
      async getFriendLink(){
        let {code,rows} = await api.getFriendLink({
            pageNum: 1,
            pageSize: 100,
        })
        console.log('rows=>',rows)
        if(code==200){
          console.log(rows)
        }
      }
    },
    mounted() {
      this.getFriendLink();
    },
  }
</script>
<style scoped lang="scss">
  .hover-hand {
    cursor: pointer;
  }

  .content {
    font-family: '9';
    font-size: 20px;
    color: rgba(255, 255, 255, 0.8);
    padding: 30px 20px;
    line-height: 28px;

    b {
      color: #fff;
      font-size: 24px;
    }

    .content-extra {
      padding: 15px 0px;
      background: #faf9e9;
      color: #000;
      text-align: center;

      b {
        color: #000;
      }
    }
  }
</style>
<style scoped lang="scss">
  @media screen and (max-width: 750px) {
    .content {
      font-size: 14px;

      b {
        font-size: 16px;
      }
    }
  }

</style>

